<template>
  <div class="home">
    <post-card
      v-for="{ _attributes } in results"
      :key="_attributes.id"
      :attributes="_attributes"
    ></post-card>
  </div>
</template>

<script>
import postCard from "../components/postCard";

export default {
  name: "home",
  components: { postCard },
  data() {
    return {
      results: []
    };
  },
  mounted() {
    this.axios
      .get("https://r34-backend.herokuapp.com/limit=50")
      .then(response => {
        this.results = response.data.post;
      });
  }
};
</script>

<style scoped>
.home {
  display: flex;
  place-items: center;
  flex-wrap: wrap;
}
</style>

