<template>
  <div class="ctn">
    <v-combobox
      v-model="model"
      :items="items"
      :search-input.sync="search"
      hide-selected
      label="Search..."
      multiple
      hide-details
      single-line
      class="pa-1 searchbox"
      small-chips
    >
      <template slot="no-data">
        <v-list-tile>
          <v-list-tile-content>
            <v-list-tile-title>
              No results matching "<strong>{{ search }}</strong>". Press <kbd>enter</kbd> to create a new one
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </template>
    </v-combobox>
    <div class="search">
      <post-card
        v-for="{ _attributes } in results"
        :key="_attributes.id"
        :attributes="_attributes"
      ></post-card>
    </div>
  </div>
</template>

<script>
import postCard from "../components/postCard";

export default {
  name: "search",
  components: { postCard },
  computed: {
    model: {
      set(value) {
        this.$store.commit("setSearchModel", value);
      },
      get() {
        return this.$store.state.searchModel;
      }
    }
  },
  data() {
    return {
      results: [],
      items: [],
      search: null
    };
  },
  watch: {
    search(val) {
      if (val === null) return (this.items = []);
      this.axios
        .get("https://r34-backend.herokuapp.com/autocomplete/" + val)
        .then(response => {
          this.items = response.data.map(e => e.label);
        });
    },
    model(val) {
      if (val === []) {
        this.axios
          .get("https://r34-backend.herokuapp.com/limit=50")
          .then(response => {
            this.results = response.data.post;
          });
        return;
      }
      let tags = val.map(e => e.split(" ")[0]).join("+");
      this.axios
        .get("https://r34-backend.herokuapp.com/limit=50&tags=" + tags)
        .then(response => {
          this.results = response.data.post;
        });
    }
  },
  mounted() {
    if (this.model === []) {
      this.axios
        .get("https://r34-backend.herokuapp.com/limit=50")
        .then(response => {
          this.results = response.data.post;
        });
      return;
    }
    let tags = this.model.map(e => e.split(" ")[0]).join("+");
    this.axios
      .get("https://r34-backend.herokuapp.com/limit=50&tags=" + tags)
      .then(response => {
        this.results = response.data.post;
      });
  }
};
</script>

<style>
.ctn {
  max-width: 100vw;
}

.search {
  display: flex;
  place-items: center;
  flex-wrap: wrap;
}

.searchbox .v-menu {
  position: absolute;
}

.searchbox .v-menu .v-menu__content {
  min-width: 0px !important;
  width: calc(100vw * 0.95);
}
</style>

