import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		media: {},
		searchModel: []
	},
	mutations: {
		setMedia(state, media) {
			state.media = media;
		},
		setSearchModel(state, model) {
			state.searchModel = model;
		}
	},
	actions: {}
});
