<template>
  <div>
    <img
      :src="imgUrl"
      :alt="media.tags"
      class="full-width"
      v-if="isImage"
    >
    <video
      v-else
      controls
      class="full-width"
    >
      <source
        :src="imgUrl"
        type="video/webm"
      >
    </video>
    <div class="text-xs-center">
      <v-chip
        v-for="tag in tags"
        :key="tag"
        color="green darken-3"
        text-color="white"
      >{{ tag }}</v-chip>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    media() {
      return this.$store.state.media;
    },
    imgUrl() {
      return `https://r34-backend.herokuapp.com/post/${this.media.id}`;
    },
    tags() {
      if (typeof this.media.tags === "string")
        return this.media.tags.trim().split(" ");
      return this.media.tags;
    },
    isImage() {
      if (!("file_url" in this.media)) return true;
      if (this.media.file_url.match(/(gif|jpg|jpeg|png|webp)$/gi)) {
        return true;
      }
      return false;
    }
  },
  mounted() {
    window.scrollTo(0, 0);
    if (!("id" in this.$route.params) && !("id" in this.media)) {
      this.$router.push("/");
    }
    if ("id" in this.$route.params) {
      this.axios
        .get(`https://r34-backend.herokuapp.com/tags/${this.$route.params.id}`)
        .then(response => {
          this.$store.commit("setMedia", response.data);
        });
    }
  }
};
</script>

<style scoped>
.full-width {
  max-width: 100vw;
}
</style>
