<template>
  <v-flex
    xs6
    class="pt-2 px-1"
    @click="setMedia(attributes)"
  >
    <v-card
      color="green darken-3"
      class="white--text"
    >
      <v-layout>
        <v-flex xs12>
          <v-img
            :src="attributes.preview_url"
            class="my-2 rounded-borders"
          ></v-img>
        </v-flex>
      </v-layout>
    </v-card>
  </v-flex>
</template>

<script>
export default {
  props: ["attributes"],
  methods: {
    setMedia(media) {
      this.$store.commit("setMedia", media);
      this.$router.push(`/view/${media.id}`);
    }
  }
};
</script>

