      icon
      @click="goBack"
    >
      <v-icon>keyboard_arrow_left</v-icon>
    </v-btn>
    <v-toolbar-title class="white--text">Rule 34</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-text-field
      v-if="searchBar"
      single-line
      hide-details
      placeholder="Search"
      class="pt-0"
    ></v-text-field>
    <v-btn
      icon
      to="/search"
    >
      <v-icon>search</v-icon>
    </v-btn>
  </v-toolbar>
</template>

<script>
export default {
  name: "appNavbar",
  methods: {
    toggleSearch() {
      this.searchBar = !this.searchBar;
    },
    goBack() {
      this.$router.go(-1);
    }
  },
  data() {
    return {
      searchBar: false
    };
  }
};
</script>

